import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { CneeSelectDemoComponent } from "app/showcase/custom-components/cnee-select-demo/cnee-select-demo.component";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { CneeSelectModule } from "app/custom-components/cnee-select/cnee-select.component";
import { CneeSelectDemoRoutingModule } from "app/showcase/custom-components/cnee-select-demo/cnee-select-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { CneeSelectDemoBasicComponent } from "app/showcase/custom-components/cnee-select-demo/cnee-select-demo-basic.component";
import { CneeSelectDemoObjectComponent } from "app/showcase/custom-components/cnee-select-demo/cnee-select-demo-object.component";

@NgModule({
	declarations:[
		CneeSelectDemoComponent,
		CneeSelectDemoBasicComponent,
		CneeSelectDemoObjectComponent
	],
	imports: [
		CneeSelectDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		CneeSelectModule
	]
})
export class CneeSelectDemoComponentModule { }



